import './App.css';

import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import Box from '@mui/material/Box';
import { useState, useEffect } from 'react';
import { alpha } from '@mui/material/styles';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import Stack from '@mui/material/Stack';
import LoginButton from './components/LoginButton';
import commonUtils from './utils';
import { TopBarDialog } from './components/topbar/utils';
import About from './components/about';
import Contact from './components/contact';

import Main from './components/main';
import b1 from './static/images/b1.webp';
import b2 from './static/images/b2.webp';
import b3 from './static/images/b3.webp';
import b4 from './static/images/b4.webp';
import b12000 from './static/images/b1-2000.webp';
import b22000 from './static/images/b2-2000.webp';
import b32000 from './static/images/b3-2000.webp';
import b42000 from './static/images/b4-2000.webp';

function AppBarLink({ text, component, wdth }) {
  const [open, setOpen] = useState(false);

  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  return (
    <>
      <Button
        onClick={handleOpen}
        sx={{ color: 'button.main' }}
      >
        <Typography variant="h6">
          {text}
        </Typography>
      </Button>
      <TopBarDialog
        text={text}
        wdth={wdth}
        open={open}
        onClose={handleClose}
        onClick={handleClose}
        sx={{ background: alpha('#000', 0.4) }}
      >
        {component}
      </TopBarDialog>
    </>
  );
}

function AppBarStack({ justify, children }) {
  return (
    <Stack
      direction="row"
      justifyContent={justify}
      alignItems="center"
      spacing={2}
      sx={{
        background: alpha('#000', 0.4),
        width: '100vw',
        position: 'fixed',
        height: '80px',
        top: 0,
        borderRight: '20px solid transparent',
        boxSizing: 'border-box',
      }}
    >
      {children}
    </Stack>
  );
}

function MyMemoriesButton() {
  return (
    <Button
      type="submit"
      variant="contained"
      sx={{
        borderRadius: '5px',
        backgroundColor: 'white',
        '&:hover': {
          backgroundColor: 'form.input',
        },
      }}
    >
      <Link style={{ textDecoration: 'none' }} to="/memories">
        <Typography variant="h4" color="primary">My Memories</Typography>
      </Link>
    </Button>
  );
}

function Actions({ authURL }) {
  const cookieValue = commonUtils.getUserInfo();
  const isLoggedIn = cookieValue !== '';
  if (isLoggedIn === true) {
    return (
      <AppBarStack justify="flex-end">
        <AppBarLink text="About" wdth="lg" component={<About />} />
        <AppBarLink text="Contact Us" wdth="sm" component={<Contact />} />
        <MyMemoriesButton />
      </AppBarStack>
    );
  }
  return (
    <AppBarStack justify="flex-end">
      <AppBarLink text="About" wdth="lg" component={<About />} />
      <AppBarLink text="Contact Us" wdth="sm" component={<Contact />} />
      <LoginButton authURL={authURL} />
    </AppBarStack>
  );
}

function App({ authURL }) {
  const urls = [
    [b1, `${b12000} 1500w, ${b1} 2000w`],
    [b2, `${b22000} 1500w, ${b2} 2000w`],
    [b3, `${b32000} 1500w, ${b3} 2000w`],
    [b4, `${b42000} 1500w, ${b4} 2000w`],
  ];

  const [image, setImage] = useState(0);
  const [display, setDisplay] = useState('none');

  useEffect(() => {
    const timer = setTimeout(() => setImage((index) => (index + 1) % urls.length), 5000);
    return () => clearTimeout(timer);
  });
  return (
    <div className="App" style={{ display, backgroundColor: 'app.background' }}>
      <Box
        component="img"
        src={urls[image][0]}
        srcSet={urls[image][1]}
        alt="Background"
        width="100%"
        height="100vh"
        onLoad={() => setDisplay('block')}
        sx={{
          positon: 'absolute',
          top: 0,
          bottom: 0,
          objectFit: 'cover',
        }}
      />
      <Actions authURL={authURL} />
      <Main authURL={authURL} />
    </div>
  );
}

App.propTypes = {
  authURL: PropTypes.string.isRequired,
};

Actions.propTypes = {
  authURL: PropTypes.string.isRequired,
};

AppBarLink.propTypes = {
  text: PropTypes.string.isRequired,
  component: PropTypes.node.isRequired,
  wdth: PropTypes.string.isRequired,
};

AppBarStack.propTypes = {
  justify: PropTypes.string.isRequired,
  children: PropTypes.node.isRequired,
};

export default App;
